import axios from 'axios'
import {ForumUrl, UploadUrl} from '../config/urls'

// 获取论坛列表
export const getForumList = (createPosts) => {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.getForumLabelList, {newpost: createPosts})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 获取论帖子详情
export const getPostsInfo = (ID, page) => {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.getPostsDetail, {forum_id: ID, page: page})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 获取论坛列表帖子
export const getForumPosts = (labelID, page) => {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.getForumPostsList, {label_id: labelID, page: page})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 获取论坛页广告
export const getForumAds = function () {
  return new Promise((resolve, reject) => {
    axios.get(ForumUrl.forumAd)
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 发送评论
export const sendPostsReply = (forumID, replyContent, PID) => {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.sendForumPostsReply, {forum_id: forumID, content: replyContent, pid: PID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 发帖
export const createPostsFn = (labelID, title, content, forumId) => {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.createPosts, {label_id: labelID, title: title, content: content, forum_id: forumId})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

