// 签到
export const checkInForum = () => {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.forumCheckIn)
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * @param {int} replyID 回复ID
 * */
export const deleteReplyContent = replyID => {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.deleteReply, {reply_id: replyID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 论坛头部操作按钮
 * @param types 类型 {apply:待审核, create_time 发表时间, browse 浏览量, reply_amount 回复数量}
 * @param labelID 论坛id
 * */
export const forumHeaderBtn = (types, labelID) => {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.getForumPostsList, {type: types, label_id: labelID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 版主确认
 * @param {int} labelID 论坛ID
 * */
export const moderatorConfirm = (labelID) => {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.moderatorConfirm, {label_id: labelID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 上传图片
 * */
export const upload = function (file) {
  return new Promise((resolve, reject) => {
    axios.post(UploadUrl.uploadImg, file, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * @desc 论坛举报接口
 * @param {int} ID 帖子或回复ID
 * @param {int} type 要举报内容的类型 1 为帖子， 2为回复
 * @param {string} reason 举报内容
 * */
export const forumAccusation = function (ID, type, reason) {
  return new Promise((resolve, reject) => {
    axios.post(ForumUrl.forumReport, {id: ID, type: type, reason: reason})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}



