<template>
  <div class="forum-header-wrap">
    <div class="wrap">
      <div class="menu-icon item">
        <i @click="clickMenu" class="fa fa-bars" aria-hidden="true"></i>
      </div>
      <h1 class="item title">重卡汇-论坛</h1>
      <router-link to="/message" class="icons item">
        <i class="fa fa-bell" aria-hidden="true"></i>
      </router-link>
    </div>
    <div class="menu-box" v-show="showMenu">
      <router-link :to="'/forum/list/' + item.label_id"
        v-for="(item, index) in labels"
        :key="index"
      >{{item.cn}}</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Forum-Header',
  props: {
    labels: Array
  },
  data () {
    return {
      showMenu: false
    }
  },
  methods: {
    clickMenu () {
      this.showMenu = !this.showMenu
    }
  }
}
</script>

<style lang="less" scoped>
  .forum-header-wrap{
    position: fixed;
    top:0;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 1rem;
    border-bottom: 1px solid #eee;
    background-color: cornflowerblue;
    z-index: 10;
    .wrap{
      display: flex;
      padding: .5rem;
      .item{
        color: #fff;
        vertical-align: middle;
      }
      .title{
        flex: 1;
        font-size: 1rem;
        padding: 0;
        margin: 0;
        text-align: center;
        color: #fff;
      }
      .icons {
        margin-top: .3rem;
        i {
          font-size: 1rem;
          vertical-align: top;
        }
        img {
          width: 1.1rem;
          height: 1.1rem;
          border-radius: 50%;
        }
      }
    }
    .menu-box{
      display: flex;
      flex-wrap: wrap;
      padding: .5rem;
      width: 100%;
      border-color: #F0F4F6 !important;
      box-shadow: 0 1px 1px rgba(0,0,0,0.11);
      margin: 0;
      background: #FFF;
      a {
        color: #333;
        padding: .4rem;
        border-bottom: 3px solid #FFF;
      }
    }
  }
</style>



