<template>
  <div class="create-wrap">
    <forum-header
      :labels="labels"
    ></forum-header>
    <div class="create-main">
      <div class="head">
        <router-link to="/forum">论坛</router-link>
        /
        <span>新建帖子</span>
      </div>
      <div class="select-box">
        <label>
          请选择板块：
          <select v-model="label">
            <option :value="item.label_id" v-for="(item, index) in labels" :key="index">{{item.cn}}</option>
          </select>
        </label>
      </div>
      <div class="cont-wrap">
        <div class="title">
          <input type="text" placeholder="请输入标题" v-model="title">
        </div>
        <div class="cont">
          <div class="editor" id="editor" v-model="content" contenteditable="true"></div>
        </div>
        <div class="clearfix btn">
          <div class="uploader-box" style="width: 3rem; height: 3rem;">
            <input type="file" ref="upfile" id="file" v-on:change="uploadFile">
          </div>
          <a v-if="showCreateBtn" href="javascript:void(0)" class="send-btn" @click="sendPosts"><strong>发表新帖</strong></a>
          <a v-else="showCreateBtn" href="javascript:void(0)" class="send-btn" @click="sendEditPosts"><strong>提交</strong></a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ForumHeader from '../forum-header.vue'
import {getLocalStorage, getQueryString} from '../../../lib/util'
import {upload, createPostsFn, getPostsInfo} from '../../../api/forum'
import {Toast} from 'mint-ui'
export default {
  name: 'Posts-Create',
  data () {
    return {
      labels: [],
      label: '',
      title: '',
      content: '',
      showCreateBtn: true
    }
  },
  created () {
    this.getLabels()
    if (this.$route.params.id === 'edit') {
      this.showCreateBtn = false
      let forumID = getQueryString('forum_id')
      getPostsInfo(forumID)
        .then(data => {
          this.title = data.data.info.title
          this.label = data.data.info.label_id
          document.getElementById('editor').innerHTML = data.data.info.content
        })
    }
  },
  methods: {
    getLabels () {
      this.$set(this, 'labels', JSON.parse(getLocalStorage('labels')))
    },
    sendPosts () {
      if (!this.label || !this.title) {
        return Toast('请输入完整')
      }
      let content = document.getElementById('editor').innerHTML
      createPostsFn(this.label, this.title, content)
        .then(data => {
          if (data.state) {
            Toast('发帖成功')
            this.$router.replace('/forum/list/' + this.label)
          } else {
            Toast(data.erormsg || '发帖失败')
          }
        })
    },
    sendEditPosts () {
      if (!this.label || !this.title) {
        return Toast('请输入完整')
      }
      let content = document.getElementById('editor').innerHTML
      createPostsFn(this.label, this.title, content, getQueryString('forum_id'))
        .then(data => {
          if (data.state) {
            Toast('修改成功')
            this.$router.replace('/forum/list/' + this.label)
          } else {
            Toast(data.erormsg || '发帖失败')
          }
        })
    },
    uploadFile () {
      let formData = new FormData()
      formData.append('0', this.$refs.upfile.files[0])
      upload(formData)
        .then(data => {
          if (data.state) {
            let img = document.createElement('img')
            img.setAttribute('src', data.data.url)
            document.getElementById('editor').appendChild(img)
          } else {
            Toast(data.errormsg || '上传失败')
          }
        })
        .catch(err => console.log(err))
    }
  },
  components: {
    ForumHeader
  }
}
</script>

<style lang="less" scoped>
  .create-wrap{
    height: 100%;
    background-color: #e5e5e5;
    .create-main{
      width: 100%;
      position: absolute;
      padding-top: 50px;
      overflow: hidden;
      background-color: #e5e5e5;
      &:after{
        content: " ";
        height: 5rem;
        display: block;
      }
      .head{
        padding: 10px;
        background-color: #f6f6f6;
        border-radius: 3px 3px 0 0;
        font-size: 14px;
        color: #999;
        a{
          color: #80bd01;
        }
      }
      .select-box{
        background-color: #fff;
        padding: 10px;
        label{
          height: 30px;
          display: block;
          line-height: 30px;
          select{
            width: 220px;
            border: 1px solid #ccc;
            display: inline-block;
            height: 30px;
            padding: 4px 6px;
            margin-bottom: 10px;
            font-size: 14px;
            line-height: 20px;
            color: #555;
            vertical-align: top;
            border-radius: 4px;
          }
        }
      }
      .cont-wrap{
        padding: 10px;
        background-color: #fff;
        .title{
          input{
            width: 98%;
            border: none;
            resize: none;
            height: 30px;
            margin-bottom: 1rem;
            border-radius: 4px;
            box-shadow: 0 0 2px rgba(60,60,60,.5);
            background: hsla(0,0%,0%,0);
          }
        }
        .cont {
          .editor{
            width: 100%;
            height: 150px;
            overflow: hidden;
            border: 1px solid #ddd;
          }
        }
        .btn {
          .send-btn{
            margin-top: 10px;
            float: right;
            margin-right: 0;
            height: 30px;
            background: #f60;
            color: white;
            font-size: 14px;
            vertical-align: middle;
            -moz-box-shadow: 0 1px 0 #E5E5E5;
            -webkit-box-shadow: 0 1px 0 #E5E5E5;
            box-shadow: 0 1px 0 #E5E5E5;
            strong{
              line-height: 30px;
              font-weight: 700;
              padding: 0 10px;
            }
          }
        }
      }
    }
  }
</style>



